import { useEffect, useRef, useState } from "react";
import {default as details} from '../details.json';
import FriendComponent from "../FriendComponent/FriendComponent";
import Messages from "../Messages/Messages";
import SearchableDropDown from "../SearchableDropdown/SearchableDropdown";
import { sendMessage, setChatList,deleteReadStatus } from "./functionsForChatPage";
const {BSON:{ObjectId}}=require('realm-web');
export default function ChatPage({realmObject}){
    const friendsList=useRef('');
    const [mongoClient,setMongoClient]=useState('');
    const [userData,setUserData]=useState({});
    const [friendsListData,setFriendsListData]=useState([]);
    const [selectedUser,setSelectedUser]=useState(''); 
    const [chatData,setChatData]=useState([]);
    useEffect(()=>{
        console.log(details,realmObject);
        let mongoClientlocal=realmObject.app.currentUser.mongoClient(details.datasourcename).db(details.databasename);
        setMongoClient(mongoClientlocal);
        mongoClientlocal.collection('chat_users').findOne({email:realmObject.user._profile.data.email}).then((res)=>{
            setUserData(res);
        }).catch(err=>console.log(err));
        let stateMethods={
            setUserData:setUserData,
            setFriendsListData:setFriendsListData,
            setSelectedUser:setSelectedUser,
            setChatData:setChatData,
            selectedUser:selectedUser,
            chatData:chatData,
            friendsListData:friendsListData,
            mongoClient:mongoClientlocal
        };
        ObjectId.toString();
        setChatList(realmObject,stateMethods,friendsList);
    },[]);
    useEffect(()=>{
        setFriendsListData([...friendsList.current]);
    },[friendsList.current]);
    return (
        <div>
            <SearchableDropDown mongoClient={mongoClient} onChange={(data)=>{
                console.log('called',data);
                let friendInList=friendsList.current.find((eachFriend)=>eachFriend.friendDetails.email === data.email);
                if(friendInList){
                    setSelectedUser(friendInList);
                    setChatData(friendInList.chatDataOfUser);
                }else{
                    friendsList.current=[{
                        index:0,
                        friendId:data._id,
                        friendDetails:data,
                        chatDataOfUser:[]
                    },...friendsList.current]
                    setSelectedUser(friendsList.current[0]);
                    setChatData([]);
                }
            }}/>
            <div style={{display:"grid",gridTemplateColumns:"30% 70%"}}>
            <div>
                {friendsListData.length>0 && 
                    friendsListData.map((eachFriend,index)=>{
                        return (
                            <FriendComponent key={index} friendData={eachFriend} onClick={(friendData)=>{
                                setSelectedUser(friendData);
                                setChatData(friendData.chatDataOfUser);
                            }}/>
                        );
                    })
                }
            </div>
            {selectedUser && 
                <Messages 
                    realmObject={realmObject} 
                    userData={userData} 
                    selectedUser={selectedUser}
                    chatData={chatData}
                    sendMessageToFriend={async (updatedText)=>{
                        await sendMessage(realmObject,userData,mongoClient,selectedUser.friendDetails,updatedText);
                    }}
                    readMessagesAction={async ()=>{
                        if(chatData.unread_messages.length === 0)
                            return;
                        chatData.read_messages=[...chatData.read_messages,...chatData.unread_messages];
                        chatData.unread_messages=[];
                        deleteReadStatus(userData,selectedUser,mongoClient);
                    }}
                />
            }
            </div>
        </div>
    );
}