export async function setChatList(realmObject,stateMethods,friendsList,mobileView=false){
    let uniqueFriendsList=await getUniqueFriendsList(stateMethods.mongoClient,realmObject.user._profile.data.email);
    uniqueFriendsList.sort((a,b)=>new Date(a.latestChatDate)>=new Date(b.latestChatDate));
    uniqueFriendsList.forEach((eachFriend)=>{eachFriend.chatDataOfUser={
        read_messages:[],
        unread_messages:[]
    }});
    console.log('entered here');
    watchCollectionForChanges(stateMethods.mongoClient,realmObject.user._profile.data.email,friendsList,stateMethods);
    for(let i=0;i<uniqueFriendsList.length;++i){
        let messages=await getMessages(uniqueFriendsList[i].friendDetails._id,realmObject,stateMethods.mongoClient,0);
        messages.forEach((eachMessage)=>{
            if(eachMessage.unreadmessage){
                uniqueFriendsList[i].chatDataOfUser.unread_messages.push(eachMessage);
            }else{
                uniqueFriendsList[i].chatDataOfUser.read_messages.push(eachMessage);
            }
        });
        console.log(uniqueFriendsList[i].chatDataOfUser);
    } 
    friendsList.current=uniqueFriendsList;
    stateMethods.setFriendsListData(uniqueFriendsList);
    //setFriendsListData(uniqueFriendsList);
    if(!mobileView && uniqueFriendsList.length>0){
        stateMethods.setSelectedUser(uniqueFriendsList[0]);
        stateMethods.setChatData(uniqueFriendsList[0].chatDataOfUser);
    }
}
export async function getUniqueFriendsList(mongoClient,email){
    console.log(email);
    let result=await mongoClient.collection(`${email}_friendsList`).aggregate([
        {$lookup:{
            from:'chat_users',
            localField:'friendId',
            foreignField:'_id',
            as:'friendDetails'
        }}
    ]);
    result=result.filter((eachFriend)=>eachFriend['friendDetails'].length>0);
    result.forEach((eachFriend)=>{
        eachFriend['friendDetails']=eachFriend['friendDetails'][0];
    });
    console.log(result);
    return result;
}
export async function getMessages(friendUserId,realmObject,mongoClient,skipCount){
    let documents=await mongoClient.collection(`${realmObject.user._profile.data.email}`).aggregate([
        {$match:{$or:[{from:friendUserId},{to:friendUserId}]}},{$skip:skipCount},{$limit:100}
    ]);
    console.log(documents);
    return documents;
}
export async function sendMessage(realmObject,userData,mongoClient,friendData,text){
    console.log(userData,friendData,text);
    let messageDate=new Date().toString();
    await mongoClient.collection(`${userData.email}`).insertOne({
        to:friendData._id,
        date:messageDate,
        text:text,
        unreadmessage:true,
    });
    await mongoClient.collection(`${friendData.email}`).insertOne({
        from:userData._id,
        date:messageDate,
        text:text,
    });
    await mongoClient.collection(`${userData.email}_friendsList`)
    .updateOne({friendId:friendData._id},
        {$set:{latestChatDate:messageDate.toString()}},
        {upsert:true}
    );
    await mongoClient.collection(`${friendData.email}_friendsList`)
    .updateOne({friendId:userData._id},
        {$set:{latestChatDate:messageDate.toString()}},
        {upsert:true}
    );
}
export async function updateChatData(friendsList,insertedData,mongoClient,stateMethods){
    console.log(stateMethods);
    let friendToUpdate=friendsList.current.find((eachFriend)=>{
        //console.log(insertedData,eachFriend,eachFriend.friendId.toString(),insertedData.fullDocument.to.toString(),
        //eachFriend.friendId.equals(insertedData.fullDocument.from ?? insertedData.fullDocument.to));
        return eachFriend.friendId.equals(insertedData.fullDocument.from ?? insertedData.fullDocument.to);
    });
    if(friendToUpdate){
        console.log('entered the block');
        friendToUpdate.chatDataOfUser.unread_messages.push(insertedData.fullDocument);
        stateMethods.setSelectedUser(friendToUpdate);
        stateMethods.setChatData({...friendToUpdate.chatDataOfUser});
    }else{
        let objectToInsert={friendId:insertedData.fullDocument.from ?? insertedData.fullDocument.to};
        console.log(objectToInsert);
        objectToInsert.chatDataOfUser={unread_messages:[insertedData.fullDocument],read_messages:[]};
        friendsList.current.push(objectToInsert);
        let friendDetails=await mongoClient.collection('chat_users').findOne({_id:objectToInsert.friendId});
        objectToInsert.friendDetails=friendDetails;
        stateMethods.setFriendsListData([...friendsList.current]);
    }
    //friendToUpdate.chatDataOfUser.unshift(insertedData.fullDocument);
} 
export async function watchCollectionForChanges(mongoClient,email,friendsList,stateMethods){
    console.log(mongoClient)
    while(true){
        try{
        let watcher=mongoClient.collection(email).watch();
        console.log(watcher);
        for await (const change of watcher){
            switch(change.operationType){
                case "insert":{
                    console.log(change);
                    updateChatData(friendsList,change,mongoClient,stateMethods)
                    break;
                }
                case "update":{
                    console.log(change);
                    break;
                }
                case "delete":{
                    console.log(change);
                    break;
                }
                case "replace":{
                    console.log(change);
                    break;
                }
                default:{
                    console.log(change);
                }
            }
        }
        }catch(err){
            console.log(err);
        }
    }
}
export async function deleteReadStatus(userData,selectedUser,mongoClient){
    let result=await mongoClient.collection(userData.email).updateMany({$and:[{to:selectedUser.friendId},{unreadmessage:{$exists:true}}]},{$unset:{unreadmessage:''}});
    console.log(result);
}