import { useEffect, useState ,useRef } from "react";
import styles from './Messages.module.scss';
export default function Messages(props) {
    const [textToSend, setTextToSend] = useState('');
    let element=useRef(null);
    useEffect(() => {
        console.log('dataChanged',props.chatData)
        setTextToSend(props.textBuffer ? props.textBuffer + '' : '');
        //setChatData([...props.selectedUser.chatDataOfUser]);
        props.readMessagesAction(props.selectedUser);
        console.log(element);
        if(element){
            element.current.scrollIntoView();
        }
    }, [props.chatData]);
    return (
        <div style={{ position: "relative" }}>
            <div className={styles.forcontainer}>
                {props.chatData &&
                    props.chatData.read_messages.map((eachChatData, index) => {
                        return (
                            <div key={index} ref={index === props.chatData.read_messages.length -1 ?element:null} className={eachChatData.from?styles.forreceiver:styles.forsender}>
                                <p style={{fontSize: "11px"}}>{new Date(eachChatData.date).toLocaleString()}</p>
                                <p>{eachChatData.text}</p>
                            </div>
                        )
                    })
                }
                {props.chatData &&
                    props.chatData.unread_messages.map((eachChatData, index) => {
                        console.log(eachChatData)
                        return (
                            <div key={index} className={eachChatData.from?styles.forreceiver:styles.forsender}>
                                <p style={{fontSize: "11px"}}>{new Date(eachChatData.date).toLocaleString()}</p>
                                <p>{eachChatData.text}</p>
                            </div>
                        )
                    })
                }
            </div>
            <div className="">
                <input type={'text'} onChange={(event) => { setTextToSend(event.target.value) }} value={textToSend} />
                <button type="button" onClick={async () => {
                    if (!textToSend) {
                        return;
                    }
                    await props.sendMessageToFriend(textToSend);
                    setTextToSend('');
                }}>Send</button>
            </div>
        </div>
    );
}